<template>
	<v-container class="pa-5">
		<h2>M_p</h2>
		<v-row class="mt-3">
			<v-col v-for="item in cards" :key="item.id" cols="12" md="4">
				<v-card class="mb-4">
					<v-img :src="item.imageUrl" height="200" />
					<v-card-title>{{ item.title }}</v-card-title>
					<v-card-subtitle>{{ item.subtitle }}</v-card-subtitle>
					<v-card-text>{{ item.description }}</v-card-text>

					<v-card-actions>
						<v-btn color="primary" @click="showDetails(item)">Details</v-btn>
					</v-card-actions>
				</v-card>
			</v-col>
		</v-row>

		<v-dialog v-model="detailsDialog" max-width="600">
			<v-card>
				<v-card-title>{{ selectedCard.title }}</v-card-title>
				<v-card-subtitle>{{ selectedCard.subtitle }}</v-card-subtitle>
				<v-card-text>{{ selectedCard.description }}</v-card-text>

				<v-card-actions>
					<v-btn color="primary" @click="closeDetails">Close</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</v-container>
</template>

<script>
	export default {
		data() {
			return {
				cards: [
					{
						id: 1,
						title: 'yohan',
						subtitle: 'type011 | type012 | type013 ',
						description: 'description 001 001 001 description 001 001 001 description 001 001 001 description 001 001 001 description 001 001 001 description 001 001 001 long long long long long long long long long ',
						imageUrl: 'https://dummyimage.com/300x300/000/fff'
					},
					{
						id: 2,
						title: 'c_profile',
						subtitle: 'sub2',
						description: 'desc002',
						imageUrl: 'https://dummyimage.com/500x500/000/fff'
					},
					{
						id: 3,
						title: 'c3',
						subtitle: 'sub3',
						description: 'desc003',
						imageUrl: 'https://dummyimage.com/1000x1000/000/fff'
					},
				],
				detailsDialog: false,
				selectedCard: {}
			}
		},
		methods: {
			showDetails(item){
				this.selectedCard = item;
				this.detailsDialog = true;
			},
			closeDetails() {
				this.detailsDialog = false;
				this.selectedCard = {};
			}
		}
	}
</script>